.real-slider {
  display: flex;
  padding-bottom: 87px;
  &__left {
    width: 50%; }
  &__right {
    width: 50%;
    color: #e7e7e7;

    text-align: center;
    position: relative; }
  &__item {
    &-name {
      font-family: $ffOpenSansLight;
      font-size: 3.286rem;
      line-height: 4rem;
      text-indent: -5px;
      padding-bottom: 26px; }
    &-list {
      color: $colorBlack07;
      font-size: 1rem;
      li {
        list-style: none;
        position: relative;
        margin-left: 13px;
        padding-bottom: 5px;

        letter-spacing: -0.2px;
        &:before {
          content: '';
          display: inline-block;
          width: 2px;
          height: 2px;
          position: absolute;
          left: -12px;
          top: 9px;
          background: $colorGreen; } } } }
  &__prev {
    position: absolute;
    left: 10px;
    top: 95px;
    display: inline-block;
    cursor: pointer;
    @include sprite($prev); }
  &__next {
    position: absolute;
    right: 10px;
    top: 95px;
    display: inline-block;
    cursor: pointer;
    @include sprite($next); }
  &__curent-slider {
    font-size: 12.79rem;
    font-family: $ffOpenSansLight;
    line-height: 15.214rem;
    &-black {
      color: #000000; } } }

.we {
  display: flex;
  align-items: center;
  padding-bottom: 8px;
  &__big {
    width: 50%;
    color: $colorGreen;
    font-size: 17.14rem;
    font-family: $ffOpenSansBold;
    letter-spacing: -3.6px;
    text-indent: -22px;
    margin-top: -77px; }
  &__list {
    width: 50%;
    font-size: 1rem;
    line-height: 1.571rem;
    color: $colorBlack07;
    ol {
      display: flex;
      flex-wrap: wrap;
      list-style: none;
      align-items: center;
      counter-reset: point;
      li {
        width: 50%;
        display: block;
        padding-bottom: 33px;
        font-size: 1rem;
        line-height: 1.500rem;
        padding-left: 50px;
        position: relative;
        &:before {
          content: counter(point);
          counter-increment: point 1;
          font-size: 2.875rem;
          font-family: $ffOpenSansLight;
          color: $colorGreen;
          min-height: 100%;
          display: block;
          position: absolute;
          left: 1px;
          top: -4px;
          padding-left: 6px;
          line-height: 3.5rem; }
        &:nth-child(2n) {
          padding-left: 55px;
          &:before {
            left: 7px; } }
        &:nth-child(5) {
          margin-top: -2px; }
        &:nth-child(6) {
          margin-top: -2px; }
        &:nth-last-child(1) {
          width: 100%;
          margin-top: -4px; } } } } }


.etblock {
  display: flex;
  padding-bottom: 48px;
  &__colum {
    width: 50%;
    &:nth-child(2) {
      padding-left: 13px; } }
  &__title {
    color: $colorGreen;
    font-family: $ffOpenSansSBold;
    font-size: 1.125rem;
    letter-spacing: 1.2px;
    padding-bottom: 13px;
    text-transform: uppercase;
    &:before {
      content: '';
      display: block; }
    &_icon_cubok {
      &:before {
        content: '';
        margin-bottom: 20px;
        @include sprite($icon-cubok); } }
    &_icon_podhod {
      &:before {
        content: '';
        margin-top: 23px;
        margin-bottom: 19px;
        @include sprite($icon-podhod); } }
    &_icon_lampa {
      &:before {
        content: '';
        margin-top: 20px;
        margin-bottom: 19px;
        @include sprite($icon-lampa); } }
    &_icon_lopata {
      &:before {
        content: '';
        margin-top: 23px;
        margin-bottom: 19px;
        @include sprite($icon-lopata); } } }
  &__desc {
    font-size: 1rem;
    color: $colorBlack07;
    line-height: 1.500rem;
    padding-bottom: 34px;
    &_icon_d1 {
      padding-left: 80px;
      position: relative;
      padding-bottom: 28px;
      &:before {
        position: absolute;
        left: 13px;
        top: 0;
        content: '';
        @include sprite($icon-desc-1); } }
    &_icon_d2 {
      padding-left: 80px;
      position: relative;
      &:before {
        position: absolute;
        left: 11px;
        top: 0;
        content: '';
        @include sprite($icon-desc-2); } }
    &_icon_d3 {
      padding-left: 80px;
      position: relative;
      padding-bottom: 28px;
      &:before {
        position: absolute;
        left: 3px;
        top: 14px;
        content: '';
        @include sprite($icon-desc-3); } }
    &_icon_d4 {
      padding-left: 80px;
      position: relative;
      &:before {
        position: absolute;
        left: 9px;
        top: 12px;
        content: '';
        @include sprite($icon-desc-4); } }
    &_icon_rvr {
      margin-top: 0;
      &:before {
        margin-top: -10px;
        margin-left: 1px;
        margin-bottom: 15px;
        display: block;
        content: '';
        @include sprite($rvr-logo); } }
    &_icon_avivi {
      padding-left: 25px;
      &:before {
        margin-top: -10px;
        margin-left: 1px;
        margin-bottom: 15px;
        display: block;
        content: '';
        @include sprite($avivi-logo); } }
    &_img_b1 {
      padding-bottom: 15px;
      img:nth-child(1) {
        margin-right: 10px; } }
    &_img_b2 {
      padding-bottom: 5px;
      img:nth-child(1) {
        margin-right: 17px; }
      img:nth-child(2) {
        margin-right: 12px; }
      img:nth-child(3) {
        margin-top: -2px; } }
    &_img_b3 {
      padding-bottom: 15px;
      img:nth-child(1) {
        margin-right: 15px; }
      img:nth-child(2) {
        margin-top: 5px; } }
    &_img_b4 {
      padding-bottom: 14px;
      img:nth-child(1) {
        margin-right: 0px;
        margin-left: -12px; }
      img:nth-child(2) {
        margin-top: -4px; } }
    &_img_b5 {
      padding-bottom: 13px;
      img:nth-child(1) {
        margin-right: 19px;
        margin-left: 3px;
        margin-top: 0; }
      img:nth-child(2) {
        margin-top: 0;
        margin-right: 25px; }
      img:nth-child(3) {
        margin-top: -6px;
        margin-right: 14px; }
      img:nth-child(4) {
        margin-top: 4px;
        margin-right: 0px; }
      img:nth-child(5) {
        margin-top: 11px;
        margin-left: 2px;
        margin-right: 13px; }
      img:nth-child(6) {
        margin-top: 11px;
        margin-right: 12px; }
      img:nth-child(7) {
        margin-top: 22px;
        margin-right: 6px; }
      img:nth-child(8) {
        margin-top: 30px;
        margin-right: 11px; }
      img:nth-child(9) {
        margin-top: 28px;
        margin-right: 0px; } } } }


.price {
  font-family: $ffOpenSansLight;
  font-size: 3.286rem;
  letter-spacing: -1px;
  vertical-align: text-bottom;
  &__summa {
    display: inline-block;
    padding-right: 20px;
    color: $colorGreen;
    font-size: 6.429rem;
    letter-spacing: -1.6px; } }


.faq {
  padding-top: 86px;
  padding-bottom: 89px;
  &__title {
